import React from "react";
//import Todoapp from "./TodoApp";
import Todo from "./Todo";


const Todos=({todos,deleteTodo,upSwap,downSwap,toggleTodo})=>{

    // console.log(todos);

    return <ul>
        {todos.map((todo)=>{
            return <Todo id={todo.id} task={todo.task} completed={todo.completed} key={todo.key} deleteTodo={deleteTodo} upSwap={upSwap} downSwap={downSwap} toggleTodo={toggleTodo}/>
        })}
    </ul>
     
  
}

export default Todos;