import {useState} from "react";

const Todotext=({addTodo})=>{
    
           

    const [inptext,setText]=useState('');


    const gettext=(event)=>{
        setText(event.target.value);
        //console.log(inptext);
    }
    const submitHandler=(e)=>{
        

        e.preventDefault();
        if(inptext!==""){
            addTodo(inptext);
            setText('');
        }
        else
        alert('Enter Something');
        


    }
    

    return <div>
        <form onSubmit={submitHandler}>
           
           <input type="text" id="Todotext" placeholder="Enter Todo" onChange={gettext} value={inptext} />
           <button type="submit">+</button>
           </form>
        </div>
}
export default Todotext;