import React,{useState} from 'react';
import Todos from './Todos';
import Todotext from './Todotext';
import { v4 as uuid } from 'uuid';
const Todoapp = () => {

    const todolist=[
        {
            id:0,
            task:"Todo1",
            completed:false,
            key:uuid()
        },
        {
            id:1,
            task:"Todo2",
            completed: true,
            key:uuid()
        },
        {
            id:2,
            task:"Todo3",
            completed:true,
            key:uuid()
        }

    ]
    const [todos,setTodo]=useState(todolist);
    const addTodo=(inptask)=>{
         setTodo((prevState)=>{
             return [...prevState,{id:prevState.length,task:inptask,completed:false,key:uuid()}]
         })

    }
    const toggleTodo=(id)=>{
        setTodo((prevState)=>{
            return prevState.map((todo)=>{
              return  id===todo.id ? {...todo,completed:!todo.completed} : todo 
            })
        })

    }
    const deleteTodo=(id)=>{

        setTodo((prevState)=>{
            return prevState.filter((todo)=>
                id!==todo.id        
            )
        })
    }
    const upSwap=(id)=>{
        // console.log("In TodoApp")
        const newArray=[...todos];
        if(id!==0){
            let temp= newArray[id];
            newArray[id]=newArray[id-1];
            newArray[id-1]=temp;
            newArray[id].id=id;
            newArray[id-1].id=id-1;
             
         setTodo(newArray);
        }
               
                
        
    }
    const downSwap=(id)=>{
        const newArray=[...todos];
        if(id!==newArray.length-1){
            let temp= newArray[id];
            newArray[id]=newArray[id+1];
            newArray[id+1]=temp;
            newArray[id].id=id;
            newArray[id+1].id=id+1; 
         setTodo(newArray);
        }
             
    }
    return (
        <div>
            <h1>Todo App</h1>
            <Todotext addTodo={addTodo}/>
            <Todos todos={todos} deleteTodo={deleteTodo} upSwap={upSwap} downSwap={downSwap} toggleTodo={toggleTodo}/>
        </div>
    );
}

export default Todoapp;
