import React,{useState} from 'react';
import {FaTrash,FaArrowUp,FaArrowDown} from 'react-icons/fa';

const Todo = ({id,task,completed,deleteTodo,upSwap,downSwap,toggleTodo}) => {
    const[checked,setChecked]=useState(completed);
    const checkboxHandler=()=>{
        console.log(checked)
        setChecked(completed);
        console.log(checked)
    }
    const deletehandler=()=>{
        deleteTodo(id);
    }
    const upSwapHandler=()=>{
        // console.log("Swap")
        upSwap(id);
    }
    const downSwapHandler=()=>{
        downSwap(id);
    }
    return (
        <li id="list"> 
            
            <span><input type="checkbox" defaultChecked={checked} onChange={()=>toggleTodo(id)} /></span>
            
           {completed ? <span style={{textDecoration:"line-through"}} >{task}</span>: <span>{task}</span> } 
            
            <span id="sp"><button onClick={deletehandler} ><FaTrash/></button><button onClick={upSwapHandler}><FaArrowUp/></button><button onClick={downSwapHandler}><FaArrowDown/></button></span>
            
            </li>
            
        
    );
}

export default Todo;
